
// @ts-ignore
import VueReadMoreSmooth from 'vue-read-more-smooth'
import Vue from 'vue'

export default Vue.extend({
  name: 'ReadMore',
  components: {VueReadMoreSmooth},
  data: () => {
    return {
      open: false,
    }
  },
  watch: {
    value(val) {
      this.open = val
    },
    open(val) {
      this.$emit('input', val)
    },
  },
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    i18nMore: {
      type: String,
      default: 'read_more.more',
    },
    i18nLess: {
      type: String,
      default: 'read_more.less',
    },
  },
})
